@charset "UTF-8";
@import "_mixins";
@import "_breakpoints";

header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    &:before{
        content: '';
        background: rgb(16,38,44);
        background: linear-gradient(0deg, rgba(16,38,44,0) 0%, rgba(16,38,44,1) 100%);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        transition: background 0.8s ease;
    }
    &.fixed{
        &:before{
            background: rgb(16,38,44);
        }
    }
    .container{
        display: flex;
        align-items: center;
        position: relative;
        height: 80px;
        nav{
            flex: 2;
        }
        a{
            color: #fff;
            text-decoration: none;
        }
    }
}
nav{
    padding: 0 50px;
    ul{
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
        > li{
            flex: 0 1 auto;
            white-space: nowrap;
            margin-right: 26px;
            &:last-child{
                flex: 7;
                text-align: right;
                margin-right: 0;
            }
            a{
                &:hover{
                    text-decoration: underline;
                }
            }
        }
    }
}

.langquage{
    .polylang_langswitcher{
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        text-transform: uppercase;
        li{
            & + li{
                &:before{
                    content: '/';
                    margin: 0 4px;
                }
            }
        }
    }
}


.search-block{
    position: absolute;
    right: 240px;
    .search{
        border-radius: 30px;
        border: 1px solid #fff;
        width: 280px;
        height: 32px;
        position: relative;
    }
    input{
        font-family: 'Source Sans Pro', sans-serif;
        padding: 0 40px 0 20px;
        color: #fff;
        appearance: none;
        height: 30px;
        line-height: 30px;
        background-color: transparent;
        border: none;
        width: 100%;
        &::placeholder{
            opacity: 1;
            color: #fff;
        }
    }
    button{
        appearance: none;
        cursor: pointer;
        background-color: transparent;
        border: none;
        position: absolute;
        right: 14px;
        top: 0;
        width: 30px;
        height: 30px;
        padding: 0;
        &:before{
            content: '';
            background-image: url("data:image/svg+xml;base64,");
            background-position: center;
            background-repeat: no-repeat;
            background-size: 16px 16px;
            width: 30px;
            height: 30px;
            display: block;
        }
        span{
            display: none;
        }
    }
}